'use client'

import { useState } from 'react'
import Link from 'next/link'
import { usePathname } from 'next/navigation'

const navItems = [
  { label: 'HOME', href: '/' },
  { label: 'BIO', href: '/bio' },
  { label: 'PORTFOLIO', href: '/selected-works' },
  { label: 'EXHIBITIONS', href: '/exhibitions' },
  { label: 'CONTACT', href: '/contact' },
]

export default function Navigation() {
  const pathname = usePathname()
  const [isOpen, setIsOpen] = useState(false)
  const isHome = pathname === '/'

  return (
    <>
      <nav style={{ 
        position: 'fixed', 
        top: 0, 
        left: 0, 
        right: 0, 
        zIndex: 120, 
        padding: '40px 60px',
        display: 'flex',
        justifyContent: 'flex-end',
        backgroundColor: 'transparent',
        pointerEvents: 'none'
      }}>
        {/* Hamburger Icon */}
        {!isOpen && (
          <button 
            onClick={() => setIsOpen(true)}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              display: 'flex',
              flexDirection: 'column',
              gap: '6px',
              pointerEvents: 'auto',
              padding: '10px'
            }}
          >
            <div style={{ width: '30px', height: '1.5px', backgroundColor: isHome ? '#fff' : '#000', transition: '0.3s' }} />
            <div style={{ width: '30px', height: '1.5px', backgroundColor: isHome ? '#fff' : '#000', transition: '0.3s' }} />
          </button>
        )}
      </nav>

      {/* Electric Blue Sidebar Menu (Exactly like Jadé's screenshot) */}
      <div style={{
        position: 'fixed',
        top: 0,
        right: 0,
        width: '50vw',
        height: '100vh',
        backgroundColor: '#0000FF',
        zIndex: 150,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'center',
        paddingLeft: '8vw',
        transform: isOpen ? 'translateX(0)' : 'translateX(100%)',
        transition: 'transform 0.6s cubic-bezier(0.19, 1, 0.22, 1)',
      }}>
        {/* Close button (X) */}
        <button 
          onClick={() => setIsOpen(false)}
          style={{
            position: 'absolute',
            top: '40px',
            right: '60px',
            background: 'none',
            border: 'none',
            cursor: 'pointer',
            padding: '10px'
          }}
        >
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M1 1L23 23M23 1L1 22.5" stroke="white" strokeWidth="2"/>
          </svg>
        </button>

        <ul style={{ 
          display: 'flex', 
          flexDirection: 'column',
          alignItems: 'flex-start',
          gap: '2.5vw', 
          listStyle: 'none', 
          margin: 0, 
          padding: 0 
        }}>
          {navItems.map((item) => (
            <li key={item.href}>
              <Link
                href={item.href}
                onClick={() => setIsOpen(false)}
                style={{
                  fontSize: 'clamp(28px, 5vw, 56px)',
                  letterSpacing: '0.15em',
                  fontWeight: 700,
                  textTransform: 'uppercase',
                  textDecoration: 'none',
                  color: '#ffffff',
                  fontFamily: 'ui-sans-serif, system-ui, -apple-system, sans-serif',
                  transition: 'opacity 0.3s ease',
                  opacity: pathname === item.href ? 1 : 0.7
                }}
              >
                {item.label}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      {/* Background Dimmer */}
      {isOpen && (
        <div 
          onClick={() => setIsOpen(false)}
          style={{
            position: 'fixed',
            inset: 0,
            backgroundColor: 'rgba(0,0,0,0.1)',
            zIndex: 140,
          }}
        />
      )}
    </>
  )
}
